import React, { useState, useEffect, useRef, useCallback } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    LineElement,
    PointElement,
    Title,
    Tooltip,
    Legend,
    Filler,
} from 'chart.js';
import { Bar, Line } from 'react-chartjs-2';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import styled from 'styled-components';
import { DEFAULT_YEAR_ITEM_NUMBER } from 'react-datepicker/dist/date_utils';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    LineElement,
    PointElement,
    Title,
    Tooltip,
    Legend,
    Filler
);

// Mock API data 
const mockHumedadData = [ { "hora": "Sat, 08 Mar 2025 20:33:10 GMT", "id": 2, "valor": 383.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:59 GMT", "id": 3, "valor": 399.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:03 GMT", "id": 4, "valor": 400.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:05 GMT", "id": 5, "valor": 397.0, "valor2": null, "valor3": null } ];
const mockLuzData = [ { "hora": "Sat, 08 Mar 2025 20:42:07 GMT", "id": 2, "valor": 395.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:13 GMT", "id": 3, "valor": 393.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:15 GMT", "id": 4, "valor": 393.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:18 GMT", "id": 5, "valor": 5.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:19 GMT", "id": 6, "valor": 389.0, "valor2": null, "valor3": null } ];
const mockProximidadData = [ { "hora": "Sat, 08 Mar 2025 20:33:07 GMT", "id": 2, "valor": 5.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:33:09 GMT", "id": 3, "valor": 11.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:33:11 GMT", "id": 4, "valor": 8.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:56 GMT", "id": 5, "valor": 7.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:58 GMT", "id": 6, "valor": 4.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:00 GMT", "id": 7, "valor": 5.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:06 GMT", "id": 8, "valor": 5.5, "valor2": null, "valor3": null } ];
const mockCo2Data = [ { "hora": "Sat, 08 Mar 2025 20:33:04 GMT", "id": 2, "valor": 385.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:33:05 GMT", "id": 3, "valor": 555.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:33:13 GMT", "id": 4, "valor": 445.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:53 GMT", "id": 5, "valor": 400.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:54 GMT", "id": 6, "valor": 115.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:02 GMT", "id": 7, "valor": 225.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:09 GMT", "id": 8, "valor": 394.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:10 GMT", "id": 9, "valor": 225.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:14 GMT", "id": 10, "valor": 365.4, "valor2": null, "valor3": null } ];
const mockTemperatureData = [ { "hora": "Sat, 08 Mar 2025 20:33:08 GMT", "id": 2, "valor": 38.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:57 GMT", "id": 3, "valor": 99.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:11 GMT", "id": 4, "valor": 24.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:12 GMT", "id": 5, "valor": 5.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:17 GMT", "id": 6, "valor": 392.0, "valor2": null, "valor3": null } ];
const mockCorrienteData = [ { "hora": "Sat, 08 Mar 2025 20:33:06 GMT", "id": 2, "valor": 383.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:33:12 GMT", "id": 3, "valor": 382.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:41:55 GMT", "id": 4, "valor": 399.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:01 GMT", "id": 5, "valor": 399.0, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:04 GMT", "id": 6, "valor": 5.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 20:42:08 GMT", "id": 7, "valor": 45.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 21:42:16 GMT", "id": 8, "valor": 575.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 21:42:16 GMT", "id": 9, "valor": 175.4, "valor2": null, "valor3": null }, { "hora": "Sat, 08 Mar 2025 21:42:16 GMT", "id": 10, "valor": 5.4, "valor2": null, "valor3": null } ];

// Styled Components for Tab Styling
const StyledTabList = styled(TabList)`
  display: flex;
  padding: 0;
  margin: 0;
  background-color:#1a2c66;
`;

const StyledTab = styled(Tab)`
  list-style: none;
  padding: 10px 20px;
  cursor: pointer;
  color: white;
  border: 1px solid #ccc;
  border-bottom: none;

  &:hover {
    color: red;
  }

  &.react-tabs__tab--selected {
    background: #fff;
    border-color: #aaa;
    color: black;
    border-radius: 5px 5px 0 0;
  }

  &:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  }
`;

const App = () => {
    const [humedadData, setHumedadData] = useState([]);
    const [luzData, setLuzData] = useState([]);
    const [proximidadData, setProximidadData] = useState([]);
    const [co2Data, setCo2Data] = useState([]);
    const [temperatureData, setTemperatureData] = useState([]);
    const [corrienteData, setCorrienteData] = useState([]);
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);
    const [area, setArea] = useState(() => {
        const storedArea = sessionStorage.getItem('luzArea');
        return storedArea ? parseFloat(storedArea) : 10;
    });
    const [apiUrl, setApiUrl] = useState(() => { 
        return sessionStorage.getItem('apiUrl') || ''; 
    });

    const chartRef = useRef(null);

    
    useEffect(() => {
        sessionStorage.setItem('apiUrl', apiUrl);
    }, [apiUrl]);


   
    const fetchData = useCallback(async (endpoint:any, setData:any, mockData:any) => {
      if (apiUrl) {
            try {
                const response = await fetch(`${apiUrl}${endpoint}`);
                if (!response.ok) {
                    console.error(`Failed to fetch ${endpoint}:`, response.status);
                    setData(mockData);  // Fallback to mock if API fails
                    return;
                }
                const data = await response.json();
                setData(data);
            } catch (error) {
                console.error(`Error fetching ${endpoint}:`, error);
                setData(mockData); // Fallback to mock data on error
            }
        }
        else {
           setData(mockData)
        }
    }, [apiUrl]);  

    useEffect(() => {
        fetchData('/api/humedad', setHumedadData, mockHumedadData);
        fetchData('/api/aire', setCo2Data, mockCo2Data);       // Corrected for Co2
        fetchData('/api/proximidad', setProximidadData, mockProximidadData);
        fetchData('/api/light', setLuzData, mockLuzData);         // Correct endpoint
        fetchData('/api/temperatura', setTemperatureData, mockTemperatureData); // Correct endpoint
        fetchData('/api/corriente', setCorrienteData, mockCorrienteData);
    }, [apiUrl, fetchData]); // fetchData is now a dependency so effect re-runs when it changes

    useEffect(() => {
        sessionStorage.setItem('luzArea', area.toString());
    }, [area]);
    interface DataItem {
        hora: string;
        id: number;
        valor: number;
        valor2: number | null;
        valor3: number | null;
    }
    const filterDataByDate = (data: DataItem[]) => {
        return data.filter((item) => {
            const itemDate = new Date(item.hora);
            return startDate !== null && endDate !== null && itemDate >= startDate && itemDate <= endDate;
        });
    };

    const filteredHumedadData = filterDataByDate(humedadData);
    const filteredLuzData = filterDataByDate(luzData);
    const filteredProximidadData = filterDataByDate(proximidadData);
    const filteredCo2Data = filterDataByDate(co2Data);
    const filteredTemperatureData = filterDataByDate(temperatureData);
    const filteredCorrienteData = filterDataByDate(corrienteData);

    const humedadChartData = {
        labels: filteredHumedadData.map(item => new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'Humedad',
                data: filteredHumedadData.map(item => item.valor),
                backgroundColor: 'rgba(54, 162, 235, 0.5)',
            },
        ],
    };

    const luzChartData = {
        labels: filteredLuzData.map(item => new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'Lumenes (Lm)',
                data: filteredLuzData.map(item => (item.valor * area) / 10000),
                backgroundColor: 'white',
                borderColor: 'blue',
                tension: 0.4,
                fill: false,
            },
        ],
    };

    const proximidadChartData = {
        labels: filteredProximidadData.map(item=> new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'Proximidad Positive',
                data: filteredProximidadData.map(item => item.valor >= 0 ? item.valor : null),
                borderColor: 'red',
                backgroundColor: 'rgba(255, 0, 0, 0.2)',
                fill: true,
                tension: 0.4,
                pointRadius: 5,
            },
            {
                label: 'Proximidad Negative',
                data: filteredProximidadData.map(item => item.valor < 0 ? item.valor : null),
                borderColor: 'blue',
                backgroundColor: 'rgba(0, 0, 255, 0.2)',
                fill: true,
                tension: 0.4,
                pointRadius: 5,
            }
        ]
    };

    const co2ChartData = {
        labels: filteredCo2Data.map(item => new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'CO2 (ppm)',
                data: filteredCo2Data.map(item => item.valor),
                borderColor: 'rgba(0, 0, 0, 0.5)',
                backgroundColor: (context:any) => {
                    const chart = context.chart;
                    const { ctx, chartArea } = chart;

                    if (!chartArea) {
                        return null;
                    }

                    const gradient = ctx.createLinearGradient(0, chartArea.bottom, 0, chartArea.top);

                    const data = context.dataset.data;
                    const maxValue = Math.max(...data);

                    const healthyThreshold = 400;
                    const criticalThreshold = 600;

                    const healthyStop = healthyThreshold / maxValue;
                    const criticalStop = criticalThreshold / maxValue;

                    gradient.addColorStop(0, 'green');

                    if(maxValue> healthyThreshold)
                        gradient.addColorStop(Math.min(healthyStop,1), 'green');
                    if(maxValue>criticalThreshold)
                        gradient.addColorStop(Math.min(criticalStop, 1), 'red');

                    gradient.addColorStop(1, 'red');

                    return gradient;
                },
                fill: true,
                tension: 0.4,
            },
        ],
    };

    const temperatureChartData = {
        labels: filteredTemperatureData.map(item => new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'Temperatura (°C)',
                data: filteredTemperatureData.map(item => item.valor),
                borderColor: (context:any) => {
                    const chart = context.chart;
                    const { ctx, chartArea } = chart;
                    if (!chartArea) {
                        return null;
                    }
                    const gradient = ctx.createLinearGradient(0, chartArea.bottom, 0, chartArea.top);
                    const data = context.dataset.data;
                    const maxValue = Math.max(...data);
                    const minValue = Math.min(...data);
                    const midValue = (maxValue + minValue) / 2;

                    const coldThreshold = midValue;
                    const hotThreshold = midValue;

                    const coldStop = (coldThreshold - minValue) / (maxValue - minValue);
                    const hotStop = (hotThreshold - minValue) / (maxValue - minValue);

                    gradient.addColorStop(0, 'blue');

                    if(maxValue> coldThreshold){
                    gradient.addColorStop(Math.max(0, Math.min(coldStop, 1)), 'blue');
                    }

                   if(maxValue> hotThreshold){
                    gradient.addColorStop(Math.min(1, Math.max(0,hotStop)), 'orange');
                   }

                    gradient.addColorStop(1, 'orange');
                    return gradient;
                },
                backgroundColor: 'rgba(255, 204, 102, 0.2)',
                fill: true,
                tension: 0.4,
            },
        ],
    };

    const corrienteChartData = {
        labels: filteredCorrienteData.map(item => new Date(item.hora).toLocaleTimeString()),
        datasets: [
            {
                label: 'Current (Amps/Volts)',
                data: filteredCorrienteData.map(item => item.valor),
                backgroundColor: (context:any) => {
                    const value = context.dataset.data[context.dataIndex];
                    if (value > 400) {
                        return 'red';
                    } else if (value < 100) {
                        return 'blue';
                    }else {
                        return 'green';
                    }
                },
            },
        ],
    };

    const chartOptions = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'Humedad ',
            },
        },
    };

    const luzChartOptions = {
        responsive: true,
        scales: {
            x: {
                ticks: {
                    color: 'black'
                },
                grid: {
                    color: 'rgba(0, 0, 0, 0.1)'
                }
            },
            y: {
                ticks: {
                    color: 'black'
                },
                grid: {
                    color: 'rgba(0, 0, 0, 0.1)'
                }
            }
        },
        plugins: {
            legend: {
                position: 'top',
                labels: {
                    color: 'black'
                }
            },
            title: {
                display: true,
                text: 'Lumenes (Lm) ',
                color: 'black'
            },
        },
        color: 'black',
    };

    const proximidadChartOptions = {
        responsive: true,
        scales: {
            x: {
                type: 'category',
                labels: filteredProximidadData.map(item => new Date(item.hora).toLocaleTimeString()),
                title: {
                    display: true,
                    text: 'Time',
                },
            },
            y: {
                title: {
                    display: true,
                    text: 'Valor',
                },
            },
        },
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'Proximidad (cm / m)',
            },
        },
    };

    const co2ChartOptions = {
        responsive: true,
        scales: {
            x: {
                type: 'category',
                labels: filteredCo2Data.map(item => new Date(item.hora).toLocaleTimeString()),
                title: {
                    display: true,
                    text: 'Time',
                },
            },
            y: {
                title: {
                    display: true,
                    text: 'CO2 (ppm)',
                },
            },
        },
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'CO2 Niveles',
            },
        },
    };

        const temperatureChartOptions = {
         responsive: true,
        scales: {
            x: {
                type: 'category',
                labels: filteredTemperatureData.map(item => new Date(item.hora).toLocaleTimeString()),
                title: {
                    display: true,
                    text: 'Time',
                },
            },
            y: {
                title: {
                    display: true,
                    text: 'Temperature (°C)',
                },
            },
        },
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'Temperatura',
            },
        },
    };

    const corrienteChartOptions = {
        responsive: true,
        scales: {
            x: {
                type: 'category',
                labels: filteredCorrienteData.map(item => new Date(item.hora).toLocaleTimeString()),
                title: {
                    display: true,
                    text: 'Time',
                },
            },
            y: {
                title: {
                    display: true,
                    text: 'Amps/Volts',
                },
            },
        },
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'Corriente vv',
            },
        },
    };

     const DateRangePicker = () => (
        <div>
            <div>
                <label>Start Date:</label>
                <DatePicker
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                    showTimeSelect
                    dateFormat="Pp"
                />
            </div>
            <div>
                <label>End Date:</label>
                <DatePicker
                    selected={endDate}
                    onChange={(date) => setEndDate(date)}
                    showTimeSelect
                    dateFormat="Pp"
                />
            </div>
        </div>
    );

    return (
        <div style={{ backgroundColor: 'white', padding: '20px', color: 'black' }}>
            <h1>Sensor gráficas 1.0 </h1>

            <DateRangePicker /> {/* Date Range Picker Here */}

            <Tabs>
                <StyledTabList>
                    <StyledTab>Humedad</StyledTab>
                    <StyledTab>Aire</StyledTab>
                    <StyledTab>Proximidad</StyledTab>
                    <StyledTab>Luz</StyledTab>
                    <StyledTab>Temperature</StyledTab>
                    <StyledTab>Corriente</StyledTab>
                    <StyledTab>Settings</StyledTab>
                </StyledTabList>

                <TabPanel>
                    <h2>Humedad</h2>

                    {filteredHumedadData.length > 0 ? (
                        <Bar data={humedadChartData}  />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                <TabPanel>
                    <h2>Aire (CO2)</h2>

                    {filteredCo2Data.length > 0 ? (
                        <Line data={co2ChartData} ref={chartRef} />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                <TabPanel>
                    <h2>Proximidad</h2>

                    {filteredProximidadData.length > 0 ? (
                        <Line data={proximidadChartData}  />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                <TabPanel>
                    <h2>Luz</h2>

                    {filteredLuzData.length > 0 ? (
                        <Line data={luzChartData}  />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                <TabPanel>
                   <h2>Temperature (°C)</h2>

                    {filteredTemperatureData.length > 0 ? (
                        <Line data={temperatureChartData}  ref={chartRef} />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                  <TabPanel>
                    <h2>Corriente (Amps/Volts)</h2>

                    {filteredCorrienteData.length > 0 ? (
                        <Bar data={corrienteChartData}  />
                    ) : (
                        <p>No data available for the selected date range.</p>
                    )}
                </TabPanel>

                <TabPanel>
                    <h2>Settings</h2>
                    <h3>Luz</h3>
                    <table>
                        <thead>
                            <tr>
                                <th>Parameter</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <th>Area (cm²)</th>
                                <td>
                                    <input
                                        type="number"
                                        value={area}
                                        onChange={(e) => setArea(parseFloat(e.target.value))}
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th>API URL</th>
                                <td>
                                    <input
                                        type="text"
                                        value={apiUrl}
                                        onChange={(e) => setApiUrl(e.target.value)}
                                    />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </TabPanel>
            </Tabs>
        </div>
    );
};

export default App;